import router from '../router';

export default {
  /**
   * id
   *
   * @param {Object} options 
   * @param {string} options.viewId id
   */
  navigateTo(options) {
    const { viewId } = options;
    router.push({ query: { id: viewId }});
  },

  /**
   * 
   */
  navigateBack() {
    router.go(-1);
  },

  /**
   * url
   *
   * @return {Object} url
   */
  getUrlParams() {
    return router.currentRoute.query;
  },

  /**
   * 
   * @param {string} title 
   */
  initTitle(subTitle) {
    const prefix = 'OA';
    document.title = prefix + (subTitle ? `\\${subTitle}` : '');
  }
};



